"use client";

import { useState, useEffect } from "react";

interface DashboardLayoutProps {
  children: React.ReactNode;
  currentSection: "products" | "users";
  onSectionChange: (section: "products" | "users") => void;
  onLogout: () => void;
}

const DashboardLayout = ({
  children,
  currentSection,
  onSectionChange,
  onLogout,
}: DashboardLayoutProps) => {
  const [hydrated, setHydrated] = useState(false);

  useEffect(() => {
    setHydrated(true);
  }, []);

  if (!hydrated) return null; // prevents SSR/client mismatch

  return (
    <div className="min-h-screen flex">
      {/* Sidebar */}
      <aside className="w-64 bg-gray-900 text-white p-4 flex flex-col">
        <h2 className="text-xl font-bold mb-6">Dashboard</h2>
        <button
          className={`mb-2 p-2 rounded ${
            currentSection === "products" ? "bg-gray-700" : "hover:bg-gray-700"
          }`}
          onClick={() => onSectionChange("products")}
        >
          Products
        </button>
        <button
          className={`mb-2 p-2 rounded ${
            currentSection === "users" ? "bg-gray-700" : "hover:bg-gray-700"
          }`}
          onClick={() => onSectionChange("users")}
        >
          Users
        </button>
        <button
          className="mt-auto p-2 rounded bg-red-600 hover:bg-red-700"
          onClick={onLogout}
        >
          Logout
        </button>
      </aside>

      {/* Main content */}
      <main className="flex-1 p-6 bg-gray-100">{children}</main>
    </div>
  );
};

export default DashboardLayout;
